*{
    box-sizing:border-box;
}
body{
    font-family: sans-serif;
    margin: 20px;
    padding: 0px;
    background-color: #B3B7EE;
}
h1{
    margin-top: 0;
    font-size: 22px;
}

h2{
    margin-top: 0;
    font-size: 20px;
}
h3{
    margin-top: 0px;
    font-size: 18px;
}
h5{
    margin-top: 0px;
    font-size: 14px;
}
h6{
    margin-top: 0px;
    font-size: 12px;
}


code {
    font-size: 1.2em;
  }
  
  ul {
    padding-inline-start: 20px;
  }
  
  * {
    box-sizing: border-box;
  }
  
  body {
    font-family: sans-serif;
    margin: 20px;
    padding: 0;
  }

  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh; /* This makes the container take the full height of the viewport */
  }
  .moveButton {
    background: #D6FF79;
    border: 2px solid #5F00BA;
    font-size: 15px;
    font-weight: bold;
    height: 50px;
    text-align: center;
    width: 120px;
    margin-top: 2px;
    border-radius: 50%;
  }
  
  .square {
    border-radius: 25%;
    background: #D6FF79;
    border: 5px solid #5F00BA;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 100px;
    height: 100px;
    margin-right: 3px;
    margin-top: 3px;
    padding: 0px;
    text-align: center;
    width: 100px;
  }
  
  .board-row:after {
    clear: both;
    content: '';
    display: table;
  }
  
  .status {
    font-size: 24px;
    font-family: sans-serif;
    font-weight: bold;
    margin-bottom: 0px;
    text-align: center;
    color: #FBF9FF

  }
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    flex-grow: 1;
  }
  .game-board {
    flex-grow: 5;
  }
  

